.CharmTable th,
.CharmTable td {
  vertical-align: middle;
}

.CharmTable th:first-child,
.CharmTable td:first-child {
  width: 40%;
}

.CharmTable th {
  background-color: #fff;
  position: -webkit-sticky;
  position: sticky;
  top: -1px;
  z-index: 1;
}

.CharmTable .CharmTable-checkboxCell {
  text-align: center;
}

.CharmTable-skills {
  display: block;
  font-size: 0.8em;
  color: #aaa;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
